<template>
 <div>
 <span v-for=" food in foodList" class='bu' :key="food.foodId"><Button  @click="updatePage(food.foodId)" type="warning">{{food.foodName}}</Button></span>  
</div>   
</template>
<script>
import {selectManyFood,selectOneFood} from '@/api/api.js'
export default {
    data(){
        return{
             canteenId:this.$route.query.canteenId,
             foodList:'',            
        }
    },
    created(){
        let data={canteenId:this.canteenId}
        selectManyFood(data).then(res=>{
            if(res.status==201){
                this.$Message.error('没有数据')
                return
            }
            this.foodList=res.data.data
        },err=>{

        })
    },
    methods:{
        updatePage(foodId){
            this.$router.push({name:'updateFood',query:{canteenId:this.canteenId,foodId:foodId}}) 
        }
    }
}
</script>
<style lang="less">
.bu{margin-left:20px}

</style>

